"use client";

import styles from "./style.module.css";
import { UniversityState } from "@/app/assets";
import Image from "next/image";
import { useState } from "react";

const UniversityStateSection = () => {
  const [currentSlide, setCurrentSlide] = useState(0);

  const slides = [1, 2, 3, 4]; // Example slides (replace with actual data if needed)

  const handlePrev = () => {
    setCurrentSlide((prev) => (prev > 0 ? prev - 1 : slides.length - 1));
  };

  const handleNext = () => {
    setCurrentSlide((prev) => (prev < slides.length - 1 ? prev + 1 : 0));
  };

  return (
    <div className={styles.UniversityStateSection}>
      {/* Static Section */}
      <div className="containerLeftRight paddingTopBottom">
        <h2 className={`${styles.HeadingName} uppercase`}>University Stats</h2>
        <div className="grid grid-cols-4 gap-4">
          <div className={styles.universityImgAlign}>
            <Image
              src={UniversityState}
              alt="icon"
              className={styles.UniversityStateSectionImg}
            />
          </div>
          <div className={styles.universityImgAlign}>
            <Image
              src={UniversityState}
              alt="icon"
              className={styles.UniversityStateSectionImg}
            />
          </div>
          <div className={styles.universityImgAlign}>
            <Image
              src={UniversityState}
              alt="icon"
              className={styles.UniversityStateSectionImg}
            />
          </div>
          <div className={styles.universityImgAlign}>
            <Image
              src={UniversityState}
              alt="icon"
              className={styles.UniversityStateSectionImg}
            />
          </div>
        </div>
      </div>

      {/* Carousel Section */}
      <div className={`${styles.carouselContainer} paddingTopBottom`}>
        <h2 className={`${styles.HeadingName} uppercase`}>
          Carousel Section
        </h2>
        <div className={`${styles.carouselWrapper} relative`}>
          <div
            className={styles.carousel}
            style={{
              transform: `translateX(-${currentSlide * 100}%)`,
              transition: "transform 0.5s ease",
            }}
          >
            {slides.map((slide, index) => (
              <div
                key={index}
                className={`${styles.carouselSlide} flex justify-center items-center`}
              >
                <Image
                  src={UniversityState}
                  alt={`Slide ${index + 1}`}
                  className={styles.UniversityStateSectionImg}
                />
              </div>
            ))}
          </div>
          <button
            className={`${styles.carouselButton} ${styles.prevButton}`}
            onClick={handlePrev}
          >
            Prev
          </button>
          <button
            className={`${styles.carouselButton} ${styles.nextButton}`}
            onClick={handleNext}
          >
            Next
          </button>
        </div>

        {/* Slide Indicators */}
        <div className={`${styles.indicators} flex justify-center mt-4`}>
          {slides.map((_, index) => (
            <button
              key={index}
              onClick={() => setCurrentSlide(index)}
              className={`${
                currentSlide === index
                  ? styles.activeIndicator
                  : styles.inactiveIndicator
              } mx-2 px-2 py-1`}
            >
              {index + 1 < 10 ? `0${index + 1}` : index + 1}
            </button>
          ))}
        </div>
      </div>
    </div>
  );
};

export default UniversityStateSection;
