
"use client"
import { useEffect, useState } from "react";
import styles from "./style.module.css";
import CloseIcon from '@mui/icons-material/Close';
import Link from "next/link";
export default function Navbar() {
  const [isMenuOpen, setIsMenuOpen] = useState(false);

  const toggleMenu = () => {
    setIsMenuOpen(!isMenuOpen);
  };
  useEffect(() => {
    const script = document.createElement('script');
 
    script.src = "https://widgets.in5.nopaperforms.com/emwgts.js";
    script.async = true;
 
    document.body.appendChild(script)
 
   
 
  }, []);
  return (
    <div>
    <div className={styles.hamburgerContainer}>
    <div className={styles.hamburger} onClick={toggleMenu}>
      <span></span>
      <span></span>
      <span></span>
    </div>
  </div>

  
  <div
    className={`${styles.sideMenu} ${isMenuOpen ? styles.menuOpen : ""}`}
  >
    <button className={styles.closeButton} onClick={toggleMenu}>
    <CloseIcon/>
    </button>
    <ul className={styles.menuList}>
    <Link href="/home" style={{ fontSize:"2vw",marginBottom:"2%" }}>Home</Link>
      <br/>
      <Link href="/about" style={{ fontSize:"2vw",marginBottom:"2%" }}>About</Link>
      <br/>
      <Link href="/" style={{ fontSize:"2vw",marginBottom:"2%" }}>Contact</Link>
     
    </ul>
  </div>
  </div>
  );
}
