import React from "react";
import { Box } from "@mui/material";
import Image from "next/image";

import styles from "./style.module.css";
import { News1, News2 } from "@/app/assets";

const News = () => {
  return (
    <div className={`${styles.NewsSection} p-0 `}>
      <div className={styles.ScrollTextBox}>
        <p className={styles.ScrollText}>
          <span className={styles.marqueeStyle}>
            &nbsp;GUSTO 2025 &nbsp; &nbsp;GUSTO 2025 &nbsp;
          </span>

          <span className={styles.marqueeStyle2}>
            &nbsp;GUSTO 2025&nbsp;&nbsp;GUSTO 2025 &nbsp;
          </span>
        </p>
      </div>
      <Box className="containerLeftRight paddingTopBottom">
        <div
          className={`${styles.newsBoxOuter} grid grid-cols-1 sm:grid-cols-2 gap-4 `}
        >
          <div className={`${styles.newsParaBox}  `}>
            <h2 className={`${styles.HeadingName}  `}> NEWS AND</h2>
            <h2 className={`${styles.HeadingName}  `}> EVENT</h2>
            <h3 className={`${styles.midumHeading}  `}>GUSTO 2024</h3>
            <p>
              Gusto 2024, hosted at IGI Campus, unfolded as a magnificent
              celebration of cultures, traditions, and diversity under the theme
              Around the World. The event showcased an unparalleled global
              adventure, uniting students in a journey of creativity, passion,
              and performance.
            </p>
            <button> Know More</button>
          </div>

          <div className={`${styles.relativeBox}  `}>
            <Image
              src={News1}
              className={`${styles.PositionImages1}  `}
              alt="Gusto 2024 Event Image"
            />

            <Image
              src={News2}
              className={`${styles.PositionImages2}  `}
              alt="Gusto 2024 Event Image"
            />
          </div>
        </div>
      </Box>
    </div>
  );
};

export default News;
