import { Box, Grid } from "@mui/material";
import React from "react";
import Image from "next/image";
import { Student } from "@/app/assets";

import styles from "./style.module.css";

const Studenttestimonials = () => {
  return (
    <div className={styles.testimonialSection}>
      <div className=" containerLeftRight paddingTopBottom ">
        <h2 className={`${styles.HeadingName} uppercase `}>
          {" "}
          Student Testimonials
        </h2>

        <Grid container spacing={2} alignItems="center">
          <Grid item xs={12} md={6} sx={{ padding: 4 }}></Grid>

          <Grid
            item
            xs={12}
            md={6}
            sx={{
              borderLeft: 2,
              display: "flex",
              justifyContent: "left",
              alignItems: "ledt",
              flexDirection: "column",
            }}
          >
            <p className={`${styles.studentpara} uppercase `}>
              {" "}
              Indira School of Business Studies PGDM gives a great opportunity
              for students to broaden their knowledge beyond their field of
              studies with lots of exposure by including a multicultural
              environment. The friendly attitude of the professors and their
              willingness to always offer a helping hand has made me feel a part
              of the ISBS family. The 2 years spent here were splendid and have
              helped me grow better professionally and personally.
            </p>

            <Box
              sx={{
                mt: 5,
              }}
            >
              <Image
                src={Student}
                className={`${styles.studentimg}  `}
                alt="Student Testimonials"
              />
            </Box>
            <Box sx={{ mt: 3 }}>
              <p className={`${styles.studentName} uppercase `}>
                {" "}
                Astha Rathi Maheshwari (PGDM-HR batch 2009-11)
              </p>
              <p className={`${styles.studentName} uppercase `}>
                {" "}
                Placed in: Nityo Infotech as Manager-Recruitment)
              </p>
            </Box>
          </Grid>
        </Grid>
      </div>
    </div>
  );
};

export default Studenttestimonials;
