import React from "react";

import styles from "./style.module.css";
import Image from "next/image";
import { FormGirl } from "@/app/assets";

const Enquiryform = () => {
  return (
    <div className={styles.testimonialSection}>
      <div className=" containerLeftRight paddingTopBottom ">
        <h2 className={`${styles.HeadingName} uppercase `}> Enquiry Form</h2>
        <div className="grid grid-cols-1 sm:grid-cols-2 gap-4  ">
          <div>
            <Image
              src={FormGirl}
              className={`${styles.ImgStyle}  `}
              alt="Student Testimonials"
            />
          </div>

          <div className={`${styles.formStyle}  `}>
            <div
              className="npf_wgts"
              data-height="400px"
              data-w="f4979ef2bea2a95c64f379b014cd2b34"
            ></div>
          </div>
        </div>
      </div>
    </div>
  );
};

export default Enquiryform;
